.jumbotron h1 {
  margin-top: 0;
}
.jumbotron .text-primary {
  color: #0095f3;
}
.panel {
  border-width: 2px;
}
.panel-tour .tour-title {
  margin-top: 22px;
}
.panel-resources .list-resources {
  margin-bottom: 0;
}
.panel-resources .list-resources > dd {
